<template>
    <div class="content">
        <div class="col-md-12 ml-auto mr-auto">
            <h2>SAMA Campaigns List</h2>
        </div>
        <div class="row mt-5">
            <div class="col-12">
                <card card-body-classes="table-full-width">
                    <h4 slot="header" class="card-title">List of Merchant Campaigns</h4>
                    <div>
                        <div class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap">
                            <el-select
                                class="select-primary mb-3 pagination-select"
                                v-model="pagination.perPage"
                                placeholder="Per page"
                            >
                                <el-option
                                    class="select-primary"
                                    v-for="item in pagination.perPageOptions"
                                    :key="item"
                                    :label="item"
                                    :value="item"
                                >
                                </el-option>
                            </el-select>
                            <el-select 
                                class="select-primary mb-3"
                                placeholder="Select Campaign Owner"
                                v-model="campaignOwnerSelecion"
                                :change="campaignOwnerChange(campaignOwnerSelecion)">
                              <el-option v-for="option in selects.campaignOwner"
                                  class="select-danger"
                                  :value="option.value"
                                  :label="option.label"
                                  :key="option.value">
                              </el-option>
                            </el-select>
                            <base-input>
                                <el-input
                                    type="search"
                                    class="mb-3 search-input"
                                    clearable
                                    prefix-icon="el-icon-search"
                                    placeholder="Search records"
                                    v-model="searchQuery"
                                    aria-controls="datatables"
                                >
                                </el-input>
                            </base-input>
                        </div>
                        <div class="col-12 d-flex flex-row-reverse">
                            <base-button 
                                type="success"
                                 @click="modals.modal1 = true"
                                icon
                            >
                                <i class="tim-icons icon-simple-add"></i>
                            </base-button>
                            <modal :show.sync="modals.modal1"
                                body-classes="p-0"
                                modal-classes="modal-dialog-centered modal-md">
                                <card type="secondary"
                                    header-classes="bg-white pb-5"
                                    body-classes="px-lg-5 py-lg-5"
                                    class="border-0 mb-0">
                                    <template>
                                        <div class="text-center text-muted mb-4">
                                            <small>Add new Campaign</small>
                                        </div>
                                        <form role="form">
                                          <el-select 
                                                class="select-danger mb-3"
                                                placeholder="Select Program"
                                                v-model="programSelecion">
                                              <el-option v-for="option in selects.types"
                                                  class="select-danger"
                                                  :value="option.value"
                                                  :label="option.label"
                                                  :key="option.value">
                                              </el-option>
                                          </el-select>
                                          <base-input alternative
                                                      class="mb-3"
                                                      placeholder="Campaign Name"
                                                      addon-left-icon="ni ni-email-83">
                                          </base-input>
                                          <base-input>
                                            <el-date-picker
                                              type="datetime"
                                              placeholder="start Date"
                                              v-model="dateTimePicker"
                                            >
                                            </el-date-picker>
                                          </base-input>
                                          <base-input>
                                            <el-date-picker
                                              type="datetime"
                                              placeholder="End Date"
                                              v-model="dateTimePicker"
                                            >
                                            </el-date-picker>
                                          </base-input>
                                          <base-checkbox class="mb-3" v-model="checked">Used by Our Business</base-checkbox>
                                          <el-select 
                                            v-show="showMerchant"
                                              class="select-danger mb-3"
                                              placeholder="Select Merchant"
                                              v-model="merchantSelecion">
                                            <el-option v-for="option in selects.merchants"
                                                class="select-danger"
                                                :value="option.value"
                                                :label="option.label"
                                                :key="option.value">
                                            </el-option>
                                          </el-select>
                                          <div class="text-center">
                                            <base-button type="primary" class="my-4" @click.native="handleAddCampaign()">Confirm</base-button>
                                          </div>
                                        </form>
                                    </template>
                                </card>
                            </modal>
                        </div>
                        <el-table :data="queriedData">
                            <el-table-column
                                v-for="column in tableColumns"
                                :key="column.label"
                                :min-width="column.minWidth"
                                :prop="column.prop"
                                :label="column.label"
                            >
                            </el-table-column>
                            <el-table-column :min-width="135" align="right" label="Active">
                                <template slot-scope="props">
                                  <base-button
                                    @click.native="handleApprove(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-badge"></i>
                                  </base-button>
                                  <base-button
                                    @click.native="handleActivation(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-attach-87"></i>
                                  </base-button>
                                  <base-button
                                     @click="handleEdit(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-pencil"></i>
                                  </base-button>
                                  <modal :show.sync="modals.editModal">
                                    <template slot="header">
                                        <h5 class="modal-title"> {{ selectedCampaign.campaignName }}</h5>
                                    </template>
                                      <base-input alternative
                                                      class="mb-3"
                                                      v-model="selectedCampaign.campaignName"
                                                      placeholder="Campaign Name"
                                                      addon-left-icon="ni ni-email-83">
                                          </base-input>
                                          <base-input alternative
                                                      v-model="selectedCampaign.terms_conditions"
                                                      placeholder="Terms and Condition"
                                                      addon-left-icon="ni ni-lock-circle-open">
                                          </base-input>
                                          <base-input alternative
                                                      v-model="selectedCampaign.isApproved"
                                                      placeholder="Approved"
                                                      addon-left-icon="ni ni-lock-circle-open">
                                          </base-input>
                                    <template slot="footer">
                                        <base-button type="secondary" @click="modals.editModal = false">Close</base-button>
                                        <base-button type="primary" @click.native="editMessage(selectedCampaign)">Save changes</base-button>
                                    </template>
                                  </modal>
                                  <base-button
                                    @click.native="handleDelete(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-trash-simple"></i>
                                  </base-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>  
                    <div
                        slot="footer"
                        class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
                    >
                        <div>
                            <p class="card-category">
                                Showing {{ from + 1 }} to {{ to }} of {{ total }} entries
                            </p>
                        </div>
                        <base-pagination
                            class="pagination-no-border"
                            v-model="pagination.currentPage"
                            :per-page="pagination.perPage"
                            :total="total"
                        >
                        </base-pagination>
                    </div>          
                </card>
            </div>
        </div>
    </div>
</template>
<script>
  import { Table, TableColumn, Select, Option, DatePicker, TimeSelect } from 'element-ui'
  import { BasePagination, BaseSwitch, Modal } from 'src/components'
  import campaigns from './campaignsList'
  import Fuse from 'fuse.js'
  import swal from 'sweetalert2'

  export default {
    name: 'Requests',
    components: {
      BasePagination,
      [Select.name]: Select,
      [Option.name]: Option,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn,
      BaseSwitch,
      Modal,
      [DatePicker.name]: DatePicker,
      [TimeSelect.name]: TimeSelect,
    },
    computed: {
      /***
       * Returns a page from the searched data or the whole data. Search is performed in the watch section below
       */
      queriedData () {
        let result = this.tableData
        console.log(result)
        if (this.searchedData.length > 0) {
          result = this.searchedData
        }
        return result.slice(this.from, this.to)
      },
      to () {
        let highBound = this.from + this.pagination.perPage
        if (this.total < highBound) {
          highBound = this.total
        }
        return highBound
      },
      from () {
        return this.pagination.perPage * (this.pagination.currentPage - 1)
      },
      total() {
        return this.searchedData.length > 0
          ? this.searchedData.length
          : this.tableData.length
      }
    },
    data() {
      return {
        dateTimePicker: '',
        showMerchant: true,
        checked: false,
        options: [],
        value: [],
        list: [],
        loading: false,
        merchants: ["Broccli", "McDonaldas", "KFC", "Thawani"],
        pagination: {
          perPage: 5,
          currentPage: 1,
          perPageOptions: [5, 10, 25, 50],
          total: 0
        },
        modals: {
            modal1: false,
            editModal: false
        },
        selects: {
            types:[
               {value: '1', label: 'Program 1'},
               {value: '2', label: 'Program 2'},
               {value: '3', label: 'Program 3'},
               {value: '4', label: 'Program 4'}],
            merchants: [
              {value: '1', label:'Merchant 1'},
              {value: '2', label:'Merchant 2'},
              {value: '3', label:'Merchant 3'}],
            campaignOwner:  [
              {value: '0', label:'All'},
              {value: '1', label:'My Campaigns'},
              {value: '2', label:'Other Campaigns'}]
        },
        searchQuery: '',
        propsToSearch: ['campaignName', 'isApproved', 'isOwner', 'active', 'merchantId'],
        tableColumns: [
          {
            prop: 'campaignName',
            label: 'Campaign Name',
            minWidth: 150
          },
          // {
          //   prop: 'isApproved',
          //   label: 'Approved from Beneficiary',
          //   minWidth: 200
          // },
          // {
          //   prop: 'isOwner',
          //   label: 'Owner',
          //   minWidth: 100
          // },
          // {
          //   prop: 'active',
          //   label: 'Active',
          //   minWidth: 100
          // },
          {
            prop: 'merchantId',
            label: 'Merchant ID',
            minWidth: 100
          }
        ],
        selectedCampaign: '',
        tableData: campaigns,
        searchedData: [],
        fuseSearch: null,
        programSelecion: null,
        merchantSelecion: null,
        campaignOwnerSelecion: null,
        actionStatus: null,
      }
    },
    methods: {
       handleEdit(index, row) {
          this.modals.editModal = true;
          this.selectedCampaign = row;
        },
        editMessage(row){
          this.actionStatus = 'edit';
          this.modals.editModal = false;
          swal({
                title: `You Edit ` + row.campaignName + ` successfully`,
                buttonsStyling: false,
                confirmButtonClass: 'btn btn-info btn-fill'
            })
        },
        handleAddCampaign() {
            this.actionStatus = 'add';
            this.modals.modal1 = false
            swal({
                title: `You Add it successfully`,
                buttonsStyling: false,
                confirmButtonClass: 'btn btn-info btn-fill'
            })
        },
      handleActivation(index, row) {
        this.actionStatus = 'activation';
        if (row.active == true) {
          swal({
            title: 'Are you sure?',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            confirmButtonText: 'Yes, Deactivate it!',
            buttonsStyling: false
          }).then(result => {
            if (result.value) {
              swal({
                title: 'Deactivated!',
                text: `You Deactivated ${row.campaignName}`,
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
          })
        } else {
          swal({
            title: 'Are you sure?',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            confirmButtonText: 'Yes, Activate it!',
            buttonsStyling: false
          }).then(result => {
            if (result.value) {
              swal({
                title: 'Activated!',
                text: `You Activated ${row.campaignName}`,
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
          })
        }
      },
      handleApprove(index, row) {
        swal({
          title: 'Are you sure?',
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Yes, Approve it!',
          buttonsStyling: false
        }).then(result => {
          if (result.value) {
            swal({
              title: 'Approved!',
              text: `You Approved ${row.campaignName}`,
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            })
          }
        })
      },
      handleDelete(index, row) {
        this.actionStatus = 'delete';
        swal({
          title: 'Are you sure?',
          text: `You won't be able to revert this!`,
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Yes, delete it!',
          buttonsStyling: false
        }).then(result => {
          if (result.value) {
            this.deleteRow(row)
            swal({
              title: 'Deleted!',
              text: `You deleted ${row.campaignName}`,
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            })
          }
        })
      },
      deleteRow(row) {
        let indexToDelete = this.tableData.findIndex(
          tableRow => tableRow.id === row.id
        )
        if (indexToDelete >= 0) {
          this.tableData.splice(indexToDelete, 1)
        }
      },
      campaignOwnerChange (value) {
        console.log(value)
        this.campaignOwnerSelecion = value
      }
    },
    mounted() {
      // Fuse search initialization.
      this.fuseSearch = new Fuse(this.tableData, {
        keys: ['brandName', 'phone', 'email', 'merchantID'],
        threshold: 0.3
      })
      this.list = this.merchants.map(item => {
        return { value: item, label: item };
      });
    },
    watch: {
      /**
       * Searches through the table data by a given query.
       * NOTE: If you have a lot of data, it's recommended to do the search on the Server Side and only display the results here.
       * @param value of the query
       */
      searchQuery(value) {
        let result = this.tableData
        if (value !== '') {
          result = this.fuseSearch.search(this.searchQuery)
        }
        this.searchedData = result
      },
      programTypeChange(value) {
        this.programSelecion = value
      },
      merchantChange (value) {
       
        this.merchantSelecion = value
      },
      checked (val) {
        if(val == true) this.showMerchant = false
        else this.showMerchant = true
      }
    }
  }
</script>
<style>
  .pagination-select,
  .search-input {
    width: 200px;
  }
</style>